	module.exports = {"data":{"markdownRemark":{"html":"<h3>Signal &#x26; Impact</h3>\n<p>We introduce Signal and Impact so that there can be a more granular understanding of hacker performance. Read our <a href=\"https://hackerone.com/blog/introducing-signal-and-impact\">blog post</a> or check out our <a href=\"https://docs.hackerone.com/hackers/signal-and-impact.html\">doc</a> to learn more.</p>\n<h3>New Default Views</h3>\n<p>We add these new default views to the inbox to better organize reports:</p>\n<ul>\n<li>Triaged</li>\n<li>Assigned to me</li>\n<li>Pending disclosure</li>\n<li>Pending bounty</li>\n</ul>\n<h3>Protective Disclosure</h3>\n<p>If the response team has evidence of active exploitation or imminent public harm, they can immediately provide remediation details to the public so that programs can take protective action.</p>\n<h3>Preview Image Attachments</h3>\n<p>Programs and hackers can now preview image attachments on the report form.</p>","frontmatter":{"path":"/changelog/2015/december","title":"December 2015"}}},"pathContext":{}}

